body {
  background-color:#eff5f8;
 
  
} 
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* font-size: calc(10px + 2vmin); */
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Card Style */

.card-body h3 {
    width: 100%;
    padding: 0;
    background-color: transparent;
    border: none;
  }

.card-header {
  font-weight: 500;
  animation: ease-in;
  margin-bottom: 15px;
  text-transform: capitalize;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 18px;
}

.card {
  width:100%;
  font-size: 14px;
  font-family: 'Times New Roman', Times, serif;
  background-color: "#ffffff";
  margin-top: 20px;
  margin-bottom: 10px;
  min-height: 228px;
  border-radius: 0.5rem;
  box-shadow: gray 1px;
  

}

path {
  pointer-events: all;
} 
 path:hover {
  opacity: 0.50;
  cursor: pointer;
}

.us-state-map {
  height: 100%;
  width: 100%;
  
}

.card-state {

  border: none;
  color: #db1515;
  background-color: transparent;

}
.form-control {
  display: inline;
 padding-left: 30px;
}

